* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.black-jack-container {
    padding-bottom: 3vh;
    background-image: url(pics/blackbackgroud.png);
    background-position: center;
    background-size: cover;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    /* Pour aligner à gauche et au centre */
}

.upper-bandeau {
    padding-top: 1vh;
    padding-left: 1vw;
    padding-bottom: 1vh;
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: space-between;
    background-image: url(pics/redcasinoback.jpg);
    border-bottom: solid 2px yellow;

}

.wallet-container {
    background-color: rgb(144, 32, 32);
    display: flex;
    align-items: center;
    justify-content: center;
    border: solid 2px yellow;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 1);
}

.wallet {
    padding-bottom: 1vh;
    padding-left: 3vh;
    padding-right: 3vh;
    color: aliceblue;
    font-size: 50px;
}

.leave-game-bj {
    top: 0;
    left: 0;
    width: 2vw;
}

.dealerCard-container {
    padding-top: 3vh;
    width: 100%;
    height: 20%;

    padding-left: 40%;
    color: aliceblue;
    font-size: 200%;
}

.dealerCard {
    display: flex;
}


.card.bj img {
    height: 295%;
}

.card {

    background-color: aliceblue;
    border: none;
    box-shadow: none;
}


.blackJackCard-container {
    display: flex;
    height: 53%;
    width: 100%;

    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
}

.cardHolder-bj {
    padding-top: 1vh;
    padding-bottom: 1vh;
    padding-left: 2vw;
    padding-right: 2vw;
    height: 86%;
    width: 50%;

    color: aliceblue;
}

.cardHolder-bj.myTurn {
    box-shadow: rgb(60, 255, 0) 0px 0px 60px;
}

.cardHolder-bj.selected {
    border: 2px solid rgb(32, 107, 44);
}

.cardHolder-bj:hover {
    border: 2px solid rgb(32, 107, 44);
}


.userName {
    transform: translateY(-20px);
}

.deck-bj {
    display: grid;
    height: 40%;
    grid-template-columns: repeat(7, 0.3vw);
    grid-gap: 10px;
}


.card1 {
    grid-column: 1;

    grid-row: 1;

}

.card2 {
    grid-column: 2;

    grid-row: 1;

}

.card3 {
    grid-column: 3;

    grid-row: 1;
}

.card4 {
    grid-column: 4;
    grid-row: 1;
}

.card5 {
    grid-column: 5;
    grid-row: 1;
}

.card6 {
    grid-column: 6;
    grid-row: 1;
}

.action-container {
    bottom: 0;
    width: 100%;
    height: 20%;
    display: flex;
    background-image: url(pics/redcasinoback.jpg);
    border-top: solid 2px yellow;

}

.bet-action-container {
    display: flex;
    padding-bottom: 2vh;
    padding-left: 1vw;
}

.bet-action-container.myTurn {
    box-shadow: rgb(60, 255, 0) 0px 0px 60px;
}

.action-button-bj {
    background-color: rgb(125, 29, 29);

    padding-left: 5vw;
    padding-right: 5vw;
    padding-top: 2vh;
    padding-bottom: 2vh;
    font-size: 2vh;
    border-radius: 8px;
    margin-right: 5vw;
    color: aliceblue;
    margin-top: 2vh;
    border: solid 2px rgb(205, 202, 55);
    box-shadow: 0 0 15px rgb(33, 32, 32);
}

.false {
    background-color: rgb(71, 49, 49);
    border: solid 2px rgb(98, 98, 52);
}

.action-button-bj:hover {
    background-color: rgb(109, 67, 67);
    cursor: pointer;
}

.bet-container {
    padding-top: 1vh;
    display: flex;
    flex-direction: column;
}

.inputBet-container {
    border-radius: 3px;
    background-color: rgb(73, 53, 53);

    display: flex;
    padding-right: 2vh;
    padding-bottom: 0.5vh;
    padding-left: 0.5vh;
    padding-top: 0.5vh;
}


.bet-giveup-container {
    padding-top: 1vh;
    display: flex;
    width: 100%;
    max-height: 70px;
    flex: 1;
    justify-content: space-between;
}

.bet-button-bj {
    background-color: rgb(32, 86, 29);
    border: solid greenyellow 2px;
    width: 100%;
    max-height: 70px;
    padding-top: 1vh;
    font-size: 27px;
    /* color: azure; */
    text-align: center;
    border-radius: 15px;
}

.bet-button-bj:hover {
    box-shadow: rgb(60, 255, 0) 0px 0px 60px;
}

.allin-button-bj {
    background-color: yellow;
    border: solid rgb(220, 185, 29) 3px;
    color: black;

    width: 100%;
    max-height: 70px;
    padding-top: 1vh;
    margin-left: 10px;
    font-size: 27px;
    text-align: center;
    border-radius: 15px;
}


.allin-button-bj:hover {
    box-shadow: rgb(246, 255, 0) 0px 0px 60px;
}

.bet-button-bj.false {
    background-color: rgb(28, 63, 26);
    border: solid rgb(84, 116, 38) 2px;
}

.bet-button-bj:hover {
    background-color: rgb(29, 77, 26);
    cursor: pointer;
    border: solid 2px yellow;
}

.giveup-button-bj {
    background-color: rgb(130, 47, 47);
    width: 100%;
    padding-top: 1vh;
    font-size: 24px;
    /* color: azure; */
    text-align: center;
    border-radius: 15px;
    margin-left: 0.5vw;
    border: solid 2px yellow;
}

.giveup-button-bj:hover {
    box-shadow: rgb(255, 58, 32) 0px 0px 60px;
}


.giveup-button-bj:hover {
    background-color: rgb(108, 45, 45);
    cursor: pointer;
}


.bj {
    color: white;
    background-color: rgb(26, 42, 53);
    font-size: 40px;
    border: none;
    border-radius: 3px;
    padding-left: 1vw;
    width: 20vw;
    height: 8vh;
}

.changeBetValue {
    display: flex;
    text-align: center;
    padding-top: 1.4vh;
    padding-left: 1.2vw;
}

.divise2 {
    font-size: 30px;
    border-radius: 10px;
    padding-top: 1vh;
    padding-right: 1vh;
    padding-left: 1vh;
    cursor: pointer;
}

.times2 {
    border-radius: 10px;
    margin-left: 4vh;
    font-size: 30px;
    padding-top: 1vh;
    padding-right: 1vh;
    padding-left: 1vh;
    cursor: pointer;
}

.slash {
    font-size: 30px;
    border-radius: 10px;
    padding-top: 1vh;
    margin-left: 4vh;
}

.divise2:hover {
    background-color: rgb(73, 87, 93);
}

.times2:hover {
    background-color: rgb(73, 87, 93);
}

.card {
    height: 11vh;
    width: 3.5vw;
    background-color: transparent;
}


.chat-container {
    z-index: 1;
    position: fixed;
    border-radius: 100px;
    bottom: 5vh;
    border-radius: 15px;
    min-height: 20vh;
    max-height: 20vh;
}

.message-container {
    overflow-wrap: break-word;
    max-height: 20vh;
    max-width: 20vw;
    border-radius: 20px;
}



.inputMessage {
    position: sticky;
    width: 100%;
    border: none;
    z-index: 1;
    bottom: 0;
    border-radius: 20px;
}



#chatContainer {
    transition: opacity 0.5s, transform 0.5s;
}

.gain-container {
    overflow-y: auto;
    width: 15%;
    height: 13%;
    position: fixed;
    /* Reste fixe lors du scroll */
    top: 50%;
    /* Position verticale au milieu */
    right: 0;
    /* Aligné à droite */
    transform: translateY(-50%);
    /* Centrer exactement au milieu verticalement */
    background-color: #000;
    /* Fond noir pour le contraste */
    color: #ffd700;
    /* Texte doré pour le thème luxe/casino */
    padding: 10px 20px;
    /* Espace intérieur pour ne pas coller au bord */
    border: 2px solid #ffd700;
    /* Bordure dorée */
    border-radius: 8px;
    /* Bordures arrondies pour un look moderne */
    font-family: 'Arial', sans-serif;
    /* Police élégante mais lisible */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    /* Ombre portée pour un effet 3D */
    z-index: 1000;
    /* S'assure que le texte reste au-dessus des autres éléments */
}

.moneyWin {
    animation: fadeIn 2s ease-in-out;
    padding: 5px;
    border-radius: 5px;
    background-color: rgba(255, 215, 0, 0.1);
    /* Légèrement différent pour distinguer les messages */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


.bl-music {
    z-index: 1000;
    /* Assure que le bouton reste au-dessus des autres éléments */
    box-sizing: border-box;
    position: fixed;
    /* Fixe le bouton dans la fenêtre de visualisation */
    bottom: 22vh;
    /* Positionnement en bas à gauche avec un peu d'espace */
    right: 2%;
    padding: 10px 20px;
    /* Espacement intérieur pour rendre le bouton plus grand et plus cliquable */
    font-size: 16px;
    /* Taille de la police pour que le texte soit clairement lisible */
    background-color: #8B0000;
    /* Un rouge profond pour un look casino luxueux */
    color: #FFD700;
    /* Couleur dorée pour le texte, évoquant le luxe */
    border: none;
    /* Supprime la bordure par défaut */
    border-radius: 5px;
    /* Bordures légèrement arrondies pour un look moderne */
    cursor: pointer;
    /* Change le curseur en main pour indiquer qu'il s'agit d'un bouton cliquable */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    /* Ombre portée pour un effet 3D subtil */
    transition: background-color 0.3s, transform 0.2s;
    /* Transition pour l'interaction */
}

.bl-music:hover {
    background-color: #640000;
    /* Assombrir le bouton au survol pour le feedback */
    transform: translateY(-2px);
    /* Effet de léger soulèvement au survol */
}

.bl-music:active {
    transform: translateY(1px);
    /* Simule un effet de pression sur le bouton */
}

@media only screen and (max-width: 768px) {
    .upper-bandeau {
        flex-direction: column;
    }

    .bet-container{
        background-image: url(pics/redcasinoback.jpg);  
        background-size: cover ;
        
    }

    .action-container{
        flex-direction: column;
    }

    .action-button-bj{
        width: 100%;
        height: 40%;
    }

    .cardHolder-bj{
        height: 80%;
        width: 80%;
    }

    img.card {
        height: 100%;
        scale: 2;
    }

    .input.bj{
        width: 100%;
    }

    .blackJackCard-container{
        height: 36%;
    }


    .chat-container,
    .bl-music{
        display: none;
    }

    .two-action-button-container{
        width: 100%;
    }
    

    
}